export async function get_tiles() {
    const response =  await fetch('http://39.105.112.161:5000/api/tiles');
    return response.json();
}

export async function get_users() {
    const response =  await fetch(`http://39.105.112.161:5000/api/users`);
    return response.json();
}

export async function light_up_block(user_id, tile_id) {
    const response =  await fetch(`http://39.105.112.161:5000/api/light_tile`,  {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify({
            user_id: user_id,
            tile_id: tile_id
        })
    });
    return response.json();
}