<template>
  <div id="app">
        <div class="container">
                <div v-if="userActive" class="user-info">
                    <p>您好，{{ username }}！请选择一个方块点亮。</p>
                </div>

                <div v-if="selectedATile">
                  <p>点亮者：{{this.selectedTile.user}}</p>

                </div>

                <div v-if="selectedTile" class="tile-selection">
                    <p>位置: {{ selectedTile.row }}-{{ selectedTile.col }}</p>

                </div>
                <button @click="lightUpTile" :disabled="!selectedTile">
                              <i class="fas fa-fire"></i> {{ button_text }}
                </button>
                <div class="zoom-controls">
                    <button @click="zoomOut">缩小图片</button>
                    <button @click="zoomIn">放大图片</button>
                </div>
                <div class="image-container">
                  <div class="tile-container">
                    <div class="tile-grid">
                        <div
                            v-for="(tile, index) in tiles"
                            :key="index"
                            class="tile"
                            :class="{ 'lit': tile.lit }"
                            :style="tile.lit ? { backgroundImage: 'url(' + tile.backgroundImage + ')', backgroundSize: '24px 24px'}: {}"
                            @click="selectTile(tile)"
                        >
                            <div v-if="tile.user" class="user-initial">
                                {{ tile.user }}
                            </div>
                        </div>
                    </div>
                  </div>


                    <div class="stats">
                        <div class="stat-item">
                            <div class="stat-value">{{ litTilesCount }}</div>
                            <div class="stat-label">已点亮</div>
                        </div>
                        <div class="stat-item">
                            <div class="stat-value">{{ totalTiles - litTilesCount }}</div>
                            <div class="stat-label">未点亮</div>
                        </div>
                        <div class="stat-item">
                            <div class="stat-value">{{ participantsCount }}</div>
                            <div class="stat-label">参与者</div>
                        </div>
                        <div class="stat-item">
                            <div class="stat-value">{{ completionPercentage }}%</div>
                            <div class="stat-label">完成度</div>
                        </div>
                    </div>

                    <div v-if="allLit" class="completed-message">
                        <h3><i class="fas fa-trophy"></i> 恭喜！</h3>
                        <p>所有方块已被点亮！</p>
                    </div>

                    <div class="user-list">
                        <h3><i class="fas fa-users"></i> 参与者列表</h3>
                        <div class="users">
                            <div v-for="(user, index) in participants" :key="index" class="user-badge">
                                {{ user.username }}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
</template>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body {
  background: linear-gradient(135deg, #1a2a6c, #b21f1f, #1a2a6c);
  color: white;
  min-height: 100vh;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: flex-start; /* 更适合移动端滚动 */
}

.container {
  width: 100%;
  max-width: 600px;
  background: rgba(0, 0, 30, 0.7);
  border-radius: 16px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.4);
  padding: 20px;
  backdrop-filter: blur(10px);
}

.user-info,
.tile-selection {
  text-align: center;
  margin-bottom: 16px;
}

.zoom-controls {
  display: flex;
  justify-content: center;
  gap: 12px;
  margin-bottom: 16px;
}

.zoom-controls button {
  padding: 8px 16px;
  font-size: 16px;
  background-color: #4fc3f7;
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: bold;
  cursor: pointer;
}

.image-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.tile-grid {
  display: grid;
  grid-template-columns: repeat(25, 1fr); /* 固定每行 25 个 */
  gap: 1px;
  margin-top: 16px;
  width: max-content; /* 宽度由内容撑开 */
  transition: transform 0.2s ease;
  transform-origin: center top;
}

.tile-container {
  width: 100%;
  height: 400px; /* 固定高度以启用垂直滚动 */
  overflow: auto;
  position: relative;
  display: flex;
  -webkit-overflow-scrolling: touch;
  align-items: flex-start;
  touch-action: pan-x pinch-zoom; /* 允许手势缩放和滑动 */
}

.tile {
  width: 24px;
  height: 24px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 0;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.tile:hover:not(.lit) {
  background: rgba(79, 195, 247, 0.3);
  transform: scale(1.1);
}

.tile.lit::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 50%, rgba(255, 255, 255, 0.3) 100%);
}

.tile.lit .user-initial {
  font-size: 8px;
  color: #fff;
  text-shadow: 0 0 2px #000;
}

.stats {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
  width: 100%;
  margin-top: 20px;
  padding: 12px;
  background: rgba(0, 10, 20, 0.6);
  border-radius: 10px;
}

.stat-item {
  text-align: center;
}

.stat-value {
  font-size: 1.4rem;
  font-weight: bold;
  color: #4fc3f7;
}

.stat-label {
  font-size: 0.8rem;
  opacity: 0.8;
}

.completed-message {
  margin-top: 20px;
  padding: 16px;
  background: rgba(46, 204, 113, 0.2);
  border-radius: 10px;
  animation: pulse 2s infinite;
  text-align: center;
}

.user-list {
  margin-top: 20px;
  width: 100%;
}

.user-list h3 {
  margin-bottom: 8px;
  color: #4fc3f7;
  font-size: 1rem;
}

.users {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}

.user-badge {
  background: rgba(79, 195, 247, 0.2);
  padding: 6px 12px;
  border-radius: 16px;
  font-size: 0.8rem;
}

/* 手机端额外样式 */
@media (max-width: 768px) {
  .image-container {
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    padding-bottom: 10px;
  }

  .tile-grid {
    min-width: 600px; /* 25 * 24px = 600px */
    transform: scale(0.7); /* 默认缩小一点 */
  }

  .tile {
    width: 24px;
    height: 24px;
    font-size: 10px;
  }

  .zoom-controls button {
    padding: 6px 12px;
    font-size: 14px;
  }

  .stats {
    grid-template-columns: repeat(2, 1fr);
  }

  .completed-message {
    font-size: 0.9rem;
  }
}
</style>
<script>
import router from "@/router";
import {get_tiles, get_users, light_up_block} from "@/api/api";
import {io} from "socket.io-client";

export default {
  el: '#app',
  data() {
    return {
      username: '',
      userActive: true,
      tiles: [],
      selectedTile: null,
      participants: [],
      totalTiles: 400,
      user: null,
      socket: null,
      zoomLevel: 1.0,
      selectedATile: false,
      button_text: '请选择一个方块',
      selected: false,
    }
  },
  computed: {
      litTilesCount() {
          return this.tiles.filter(tile => tile.lit).length;
      },
      participantsCount() {
          return this.participants.length;
      },
      completionPercentage() {
          return Math.round((this.litTilesCount / this.totalTiles) * 100);
      },
      allLit() {
          return this.litTilesCount === this.totalTiles;
      }
  },
  mounted() {
      this.initializeTiles();
      this.connectSocket();
  },
  methods: {
    zoomIn() {
        this.zoomLevel += 0.1;
        this.applyZoom();
    },
    zoomOut() {
        if (this.zoomLevel > 0.2) {
            this.zoomLevel -= 0.1;
            this.applyZoom();
        }
    },
    applyZoom() {
        const tileGrid = document.querySelector('.tile-grid');
        if (tileGrid) {
            tileGrid.style.transform = `scale(${this.zoomLevel})`;
        }
    },
    connectSocket() {
      this.socket = io("http://39.105.112.161:5000"); // 连接到 Flask SocketIO 服务

      this.socket.on("connect", () => {
        console.log("Connected to server");
      });

      this.socket.on("tile_updated", (tile) => {
        console.log("Received tile update:", tile);
        this.handleTileUpdate(tile); // 更新 UI
      });

      this.socket.on("disconnect", () => {
        console.log("Disconnected from server");
      });
    },
    handleTileUpdate(tileData) {
      const tile = this.tiles.find(t => t.id === tileData.id);
      if (tile) {
        tile.lit = true;
        tile.user = tileData.user;
        // 可以在这里触发动画或提示
      }
    },


    initializeTiles: async function () {
      // 创建8x8网格的方块
      // 如果没登陆，踹回去
      if (localStorage.getItem('user') === 'null' || localStorage.getItem('user') === null) {
        await router.push('/')
        return
      } else {
        this.username = localStorage.getItem('username')
        this.user = localStorage.getItem('user')
      }

      await get_tiles().then(res => {
        this.tiles = res
      })
      console.log('tiles', this.tiles)
      for (let i = 0; i < this.tiles.length; i++) {
        this.tiles[i].backgroundImage = require(`@/assets/tiles/piece_${this.tiles[i].row - 1}_${this.tiles[i].col - 1}.jpg`)
        // 测试：全点亮
        // this.tiles[i].lit = true
      }

      // for (let i = 0; i < 16; i++) {
      //   // 先col
      //   for (let j = 0; j < 25; j++) {
      //     // 后row
      //     this.tiles[i * 16 + j].backgroundImage = require(`@/assets/tiles/piece_${i}_${j}.jpg`)
      //     this.tiles[i * 16 + j].lit = true
      //   }
      // }

      await get_users().then(res => {
        this.participants = res
      })
      console.log('users', this.participants)
    },
      activateUser() {
          if (this.username.trim() === '') {
              alert('请输入您的姓名');
              return;
          }

          this.userActive = true;
          this.participants.push(this.username);
      },
      selectTile(tile) {
          if (!this.userActive) {
              alert('请先激活您的账号');
              return;
          }

          if (tile.lit) {
              // 展示方块的用户名以及登入时间
              this.selectedTile = tile;
              return;
          }
          this.button_text = '点亮方块'
          this.selectedTile = tile;
      },
      async lightUpTile() {
        if (!this.selectedTile) {
          alert('请先选择一个方块');
          return;
        }
        // 判断这个客户端是不是点击过了
        if (this.selected) {
          this.button_text = '您已经点亮过了'
          return;
        }
        // 在实际应用中，这里会发送请求到后端

        await light_up_block(this.user, this.selectedTile.id)
        // this.selectedTile.lit = true;
        // this.selectedTile.user = this.username;
        if (this.socket && this.socket.readyState === WebSocket.OPEN) {
          this.socket.send(JSON.stringify({
            type: 'tile_lited',
            payload: {
              tile_id: this.selectedTile.id,
              user_id: this.user,
              username: this.username
            }
          }));
        }
        // 在实际应用中，这里会广播更新给所 有用户

        // alert(`成功点亮方块 ${this.selectedTile.row}-${this.selectedTile.col}`);
        this.selected = true;
        this.selectedTile = null;
        this.button_text = '点亮成功!'
      }
  },
  beforeDestroy() {
    if (this.socket) {
      this.socket.close();
    }
  }
}
</script>